.container {
  min-height: 100vh;
}
.cart {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cartItem {
  width: 800px;
  height: 350px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  border-radius: 25px;
  margin: 30px;
}

.cartItem img {
  width: 100px;
  height: 100px;
  margin-right: 20px;
  margin-left: 20px;
  border-radius: 50%;
}
.cartItem p {
  font-size: 15px;
  color: #FFFFFF;
}

.cartItem .description {
  width: 100%;
  font-size: 30px;
}

.countHandler input {
  width: 40px;
  text-align: center;
  font-weight: bolder;
}

.checkout button {
  width: 150px;
  height: 50px;
  background-color: rgb(19, 19, 19);
  color: white;
  border: none;
  border-radius: 8px;
  margin: 10px;
  cursor: pointer;
}

.checkout p {
  color: #FFFFFF;
  font-size: medium;
  align-self: flex-end;
}